@import url('https://fonts.googleapis.com/css2?family=DM+Sans&family=Forum&family=IBM+Plex+Sans+Thai+Looped&family=Jost:ital,wght@0,400;1,300&family=Lato&family=Mulish:wght@400;600&family=Oswald&family=Rubik:wght@400;500&display=swap');
@import url('https://fonts.googleapis.com/css2?family=DM+Sans&family=Forum&family=IBM+Plex+Sans+Thai+Looped&family=Jost:ital,wght@0,400;1,300&family=Lato&family=Mulish:wght@400;600&family=Open+Sans&family=Oswald&family=Rubik:wght@400;500&display=swap');


* {
    margin: 0;
    padding: 0;
    text-decoration: none;
    list-style: none;
    box-sizing: border-box;
}

.container {
    width: 100%;
    height: auto;
    .header {
        width: 100%;
        height: 560px;
        background-image: url(./img/IMAGE.png);
        background-repeat: no-repeat;
        background-size: cover;
        .navbar-all {
            width: 100%;
            height: 100px;
            display: flex;
            align-items: center;
            justify-content: space-around;
            .logo {
                font-family: 'Lato', sans-serif;
                font-weight: 400;
                font-style: normal;
                font-size: 24px;
                line-height: 30px;
                color: rgba(255, 255, 255, 1);
            }
            .navbar {
                width: auto;
                height: auto;
                .navbar-ul {
                    display: flex;
                    align-items: center;
                    gap: 50px;
                    a {
                        font-family: 'Lato', sans-serif;
                        font-weight: 400;
                        font-style: normal;
                        font-size: 11px;
                        line-height: 17px;
                        letter-spacing: 1px;
                        color: rgba(153, 153, 153, 1);
                    }
                    & a:hover {
                        color: white;
                    }
                }
                .fa-solid {
                    display: none;
                }
            }
        }
        .navbar-home {
            width: 100%;
            height: 460px;
            display: flex;
            align-items: center;
            justify-content: center;
            flex-direction: column;
            gap: 50px;
            .home-about {
                width: 465px;
                font-family: 'Lato', sans-serif;
                font-weight: 400;
                font-style: normal;
                font-size: 48px;
                line-height: 53px;
                color: rgba(255, 255, 255, 1);
                text-align: center;
            }
            .get-btn {
                width: 137px;
                height: 39px;
                border-radius: 3px;
                border:  1px solid rgba(0, 214, 180, 1);
                background: none;
                font-family: 'Lato', sans-serif;
                font-weight: 400;
                font-style: normal;
                font-size: 13px;
                line-height: 19px;
                display: flex;
                align-items: center;
                justify-content: center;
                color: rgba(0, 214, 180, 1);
                cursor: pointer;
            }

            & .get-btn:hover {
                background-color: rgba(0, 214, 180, 1);
                color: rgba(255, 255, 255, 1);
            }
        }
    }

    .section {
        width: 100%;
        height: auto;
        background-color: rgba(34, 34, 34, 1);
        display: flex;
        align-items: center;
        flex-direction: column;
        padding: 50px 0;
        .section-title {
            font-family: 'Lato', sans-serif;
            font-weight: 400;
            font-style: normal;
            font-size: 25px;
            line-height: 30px;
            color: rgba(255, 255, 255, 1);
        }
        .section-gallery {
            width: auto;
            height: auto;
            display: grid;
            grid-template-columns: repeat(3, 1fr);
            gap: 10px;
            padding: 50px 0;
            span {
                width: 300px;
                height: 225px;
                background-color: white;
                background-repeat: no-repeat;
                background-size: 100%;
                background-position: center;
                transition: 0.3s;
                cursor: pointer;
            }
            & span:nth-child(1) {
                background-image: url(./img/IMAGE0.png);
            }
            & span:nth-child(2) {
                background-image: url(./img/IMAGE1.png);
            }
            & span:nth-child(3) {
                background-image: url(./img/IMAGE2.png);
            }
            & span:nth-child(4) {
                background-image: url(./img/IMAGE3.png);
            }
            & span:nth-child(5) {
                background-image: url(./img/IMAGE4.png);
            }
            & span:nth-child(6) {
                background-image: url(./img/IMAGE5.png);
            }
            & span:nth-child(7) {
                background-image: url(./img/IMAGE6.png);
            }
            & span:nth-child(8) {
                background-image: url(./img/IMAGE7.png);
            }
            & span:nth-child(9) {
                background-image: url(./img/IMAGE8.png);
            }

            & span:hover {
                background-size: 120%;
            }
        }
    }

    .footer {
        display: flex;
        align-items: center;
        justify-content: center;
        flex-direction: column;
        background-color: rgba(34, 34, 34, 1);
        width: 100%;
        padding: 30px;
        gap: 5px;
        .footer-title {
            font-family: 'Lato', sans-serif;
            font-weight: 400;
            font-style: normal;
            font-size: 18px;
            line-height: 22px;
            color: rgba(194, 194, 194, 1);
        }
        .footer-dev {
            font-family: 'Open Sans', sans-serif;
            font-weight: 400;
            font-style: normal;
            font-size: 12px;
            line-height: 18px;
            color: rgba(153, 153, 153, 1);
        }
    }
}

